'use client';

import { useState, useEffect } from 'react';

export default function PortfolioPage() {
  const [assets, setAssets] = useState([]);
  const [formData, setFormData] = useState({
    ticker: '',
    quantity: '',
    averagePrice: ''
  });
  const [loading, setLoading] = useState(false);
  const [portfolioSummary, setPortfolioSummary] = useState({
    totalInvested: 0,
    currentValue: 0,
    profitLoss: 0
  });

  // Simulação de preços em tempo real (em produção, você usaria a Alpha Vantage)
  const mockPrices = {
    'PETR4': 35.75,
    'VALE3': 68.90,
    'ITUB4': 32.15,
    'BTC': 250000.00,
    'IVVB11': 245.60,
    'MGLU3': 1.85,
    'BBDC4': 15.42
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (!formData.ticker || !formData.quantity || !formData.averagePrice) {
      alert('Por favor, preencha todos os campos');
      return;
    }

    const newAsset = {
      id: Date.now(),
      ticker: formData.ticker.toUpperCase(),
      quantity: parseFloat(formData.quantity),
      averagePrice: parseFloat(formData.averagePrice),
      currentPrice: mockPrices[formData.ticker.toUpperCase()] || 0,
      lastUpdated: new Date().toLocaleString('pt-BR')
    };

    setAssets([...assets, newAsset]);
    setFormData({ ticker: '', quantity: '', averagePrice: '' });
  };

  const handleDelete = (id) => {
    setAssets(assets.filter(asset => asset.id !== id));
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // Atualizar preços e cálculos
  useEffect(() => {
    const totalInvested = assets.reduce((sum, asset) => 
      sum + (asset.quantity * asset.averagePrice), 0
    );
    
    const currentValue = assets.reduce((sum, asset) => 
      sum + (asset.quantity * (mockPrices[asset.ticker] || asset.currentPrice)), 0
    );

    const profitLoss = currentValue - totalInvested;

    setPortfolioSummary({
      totalInvested,
      currentValue,
      profitLoss
    });
  }, [assets]);

  const formatCurrency = (value) => {
    return new Intl.NumberFormat('pt-BR', {
      style: 'currency',
      currency: 'BRL'
    }).format(value);
  };

  const formatPercentage = (value, total) => {
    if (total === 0) return '0%';
    const percentage = ((value / total) * 100).toFixed(2);
    return `${percentage}%`;
  };

  return (
    <div className="max-w-7xl mx-auto">
      {/* Header */}
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold text-white mb-4">
          Meus <span className="text-fin-gold">Investimentos</span>
        </h1>
        <p className="text-gray-300 text-lg">
          Acompanhe sua carteira de investimentos em tempo real
        </p>
      </div>

      {/* Cards de Resumo com Dados Dinâmicos */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <div className="bg-fin-card rounded-xl p-6 border border-fin-card/30 shadow-lg">
          <div className="flex items-center justify-between">
            <div>
              <h3 className="text-gray-400 text-sm mb-2">Valor Total Investido</h3>
              <p className="text-2xl font-bold text-white">
                {formatCurrency(portfolioSummary.totalInvested)}
              </p>
            </div>
            <div className="text-fin-gold bg-fin-gold/10 p-3 rounded-lg">
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8c-1.657 0-3 .895-3 2s1.343 2 3 2 3 .895 3 2-1.343 2-3 2m0-8c1.11 0 2.08.402 2.599 1M12 8V7m0 1v8m0 0v1m0-1c-1.11 0-2.08-.402-2.599-1" />
              </svg>
            </div>
          </div>
        </div>

        <div className="bg-fin-card rounded-xl p-6 border border-fin-card/30 shadow-lg">
          <div className="flex items-center justify-between">
            <div>
              <h3 className="text-gray-400 text-sm mb-2">Valor Atual</h3>
              <p className="text-2xl font-bold text-white">
                {formatCurrency(portfolioSummary.currentValue)}
              </p>
            </div>
            <div className="text-blue-400 bg-blue-400/10 p-3 rounded-lg">
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7h8m0 0v8m0-8l-8 8-4-4-6 6" />
              </svg>
            </div>
          </div>
        </div>

        <div className="bg-fin-card rounded-xl p-6 border border-fin-card/30 shadow-lg">
          <div className="flex items-center justify-between">
            <div>
              <h3 className="text-gray-400 text-sm mb-2">Lucro/Prejuízo</h3>
              <p className={`text-2xl font-bold ${
                portfolioSummary.profitLoss >= 0 ? 'text-fin-highlight' : 'text-red-400'
              }`}>
                {formatCurrency(portfolioSummary.profitLoss)}
              </p>
              <p className={`text-sm ${
                portfolioSummary.profitLoss >= 0 ? 'text-fin-highlight' : 'text-red-400'
              }`}>
                {portfolioSummary.totalInvested > 0 && 
                  formatPercentage(portfolioSummary.profitLoss, portfolioSummary.totalInvested)
                }
              </p>
            </div>
            <div className={`p-3 rounded-lg ${
              portfolioSummary.profitLoss >= 0 ? 'text-fin-highlight bg-fin-highlight/10' : 'text-red-400 bg-red-400/10'
            }`}>
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                {portfolioSummary.profitLoss >= 0 ? (
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 10l7-7m0 0l7 7m-7-7v18" />
                ) : (
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 14l-7 7m0 0l-7-7m7 7V3" />
                )}
              </svg>
            </div>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
        {/* Formulário para Adicionar Ativos */}
        <div className="bg-fin-card rounded-xl p-6 border border-fin-card/30 shadow-lg">
          <h2 className="text-xl font-bold text-white mb-6 flex items-center">
            <svg className="w-5 h-5 mr-2 text-fin-gold" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
            </svg>
            Adicionar Ativo
          </h2>
          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-gray-300 text-sm mb-2">Ticker do Ativo</label>
              <input 
                type="text" 
                name="ticker"
                value={formData.ticker}
                onChange={handleInputChange}
                placeholder="Ex: PETR4, BTC, IVVB11"
                className="w-full bg-fin-dark border border-fin-card/30 rounded-lg px-4 py-3 text-white placeholder-gray-500 focus:outline-none focus:border-fin-highlight transition-all duration-300"
              />
              <p className="text-xs text-gray-500 mt-1">
                Sugestões: PETR4, VALE3, ITUB4, BTC, IVVB11
              </p>
            </div>
            
            <div className="grid grid-cols-2 gap-4">
              <div>
                <label className="block text-gray-300 text-sm mb-2">Quantidade</label>
                <input 
                  type="number" 
                  name="quantity"
                  value={formData.quantity}
                  onChange={handleInputChange}
                  step="0.000001"
                  placeholder="Ex: 100, 0.5"
                  className="w-full bg-fin-dark border border-fin-card/30 rounded-lg px-4 py-3 text-white placeholder-gray-500 focus:outline-none focus:border-fin-highlight transition-all duration-300"
                />
              </div>
              <div>
                <label className="block text-gray-300 text-sm mb-2">Preço Médio (R$)</label>
                <input 
                  type="number" 
                  name="averagePrice"
                  value={formData.averagePrice}
                  onChange={handleInputChange}
                  step="0.01"
                  placeholder="Ex: 35.50"
                  className="w-full bg-fin-dark border border-fin-card/30 rounded-lg px-4 py-3 text-white placeholder-gray-500 focus:outline-none focus:border-fin-highlight transition-all duration-300"
                />
              </div>
            </div>

            {formData.ticker && mockPrices[formData.ticker.toUpperCase()] && (
              <div className="bg-fin-dark/50 rounded-lg p-3 border border-fin-highlight/20">
                <p className="text-sm text-gray-300">
                  Preço atual de <strong>{formData.ticker.toUpperCase()}</strong>: {' '}
                  <span className="text-fin-highlight font-semibold">
                    {formatCurrency(mockPrices[formData.ticker.toUpperCase()])}
                  </span>
                </p>
              </div>
            )}

            <button 
              type="submit"
              disabled={loading}
              className="w-full bg-fin-highlight hover:bg-fin-highlight/90 text-white font-semibold py-3 px-6 rounded-lg transition-all duration-300 disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center"
            >
              {loading ? (
                <>
                  <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                  </svg>
                  Adicionando...
                </>
              ) : (
                <>
                  <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                  </svg>
                  Adicionar à Carteira
                </>
              )}
            </button>
          </form>
        </div>

        {/* Lista de Ativos */}
        <div className="bg-fin-card rounded-xl p-6 border border-fin-card/30 shadow-lg">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-xl font-bold text-white flex items-center">
              <svg className="w-5 h-5 mr-2 text-fin-gold" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
              </svg>
              Minha Carteira
            </h2>
            <span className="text-sm text-gray-400 bg-fin-dark px-3 py-1 rounded-full">
              {assets.length} ativo{assets.length !== 1 ? 's' : ''}
            </span>
          </div>

          {assets.length === 0 ? (
            <div className="text-center py-12">
              <div className="text-gray-400 mb-4">
                <svg className="w-16 h-16 mx-auto" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M12 8c-1.657 0-3 .895-3 2s1.343 2 3 2 3 .895 3 2-1.343 2-3 2m0-8c1.11 0 2.08.402 2.599 1M12 8V7m0 1v8m0 0v1m0-1c-1.11 0-2.08-.402-2.599-1" />
                </svg>
              </div>
              <p className="text-gray-300">Nenhum ativo cadastrado ainda</p>
              <p className="text-gray-500 text-sm mt-2">Adicione seu primeiro investimento usando o formulário ao lado</p>
            </div>
          ) : (
            <div className="space-y-4 max-h-96 overflow-y-auto">
              {assets.map((asset) => {
                const currentPrice = mockPrices[asset.ticker] || asset.currentPrice;
                const investedValue = asset.quantity * asset.averagePrice;
                const currentValue = asset.quantity * currentPrice;
                const profitLoss = currentValue - investedValue;
                const profitLossPercentage = (profitLoss / investedValue) * 100;

                return (
                  <div key={asset.id} className="bg-fin-dark rounded-lg p-4 border border-fin-card/20 hover:border-fin-highlight/30 transition-all duration-300">
                    <div className="flex justify-between items-start mb-3">
                      <div>
                        <h3 className="font-bold text-white text-lg">{asset.ticker}</h3>
                        <p className="text-sm text-gray-400">
                          {asset.quantity} un. × {formatCurrency(asset.averagePrice)}
                        </p>
                      </div>
                      <button
                        onClick={() => handleDelete(asset.id)}
                        className="text-gray-400 hover:text-red-400 transition-colors duration-200 p-1"
                      >
                        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                        </svg>
                      </button>
                    </div>
                    
                    <div className="grid grid-cols-2 gap-4 text-sm">
                      <div>
                        <p className="text-gray-400">Investido</p>
                        <p className="text-white font-semibold">{formatCurrency(investedValue)}</p>
                      </div>
                      <div>
                        <p className="text-gray-400">Valor Atual</p>
                        <p className="text-white font-semibold">{formatCurrency(currentValue)}</p>
                      </div>
                    </div>
                    
                    <div className="mt-3 pt-3 border-t border-fin-card/20">
                      <div className="flex justify-between items-center">
                        <span className="text-gray-400 text-sm">Resultado</span>
                        <div className="text-right">
                          <p className={`font-semibold ${
                            profitLoss >= 0 ? 'text-fin-highlight' : 'text-red-400'
                          }`}>
                            {formatCurrency(profitLoss)}
                          </p>
                          <p className={`text-xs ${
                            profitLoss >= 0 ? 'text-fin-highlight' : 'text-red-400'
                          }`}>
                            {profitLossPercentage.toFixed(2)}%
                          </p>
                        </div>
                      </div>
                    </div>
                    
                    <p className="text-xs text-gray-500 mt-2">
                      Atualizado: {asset.lastUpdated}
                    </p>
                  </div>
                );
              })}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}